// Grid system
.main {
  @include make-sm-column($main-sm-columns);
  .sidebar-primary & {
    @include make-sm-column($main-sm-columns - $sidebar-sm-columns);
  }

	padding: 0;



}
.sidebar {
  @include make-sm-column($sidebar-sm-columns);
}

.row {
	margin-left: 0;
	margin-right: 0;
}


@media (max-width: 768px) {
	.container {
		width: 100%;
	}
}