.container[role="document"] {
  min-height: 82%;
  background-color: $medium-gray; //rgba($medium-gray, 0.54); //$background-gray;

  	.content{
		margin-bottom: 3em;
	}
}

#front-page-tabs {
	background-color: $background-gray; //rgba($medium-gray, 0.35); $medium-gray; 
	display: flex;
	flex-direction: column;
	overflow: hidden;
	width: 100%;
	// height: 1000px;
	flex-wrap: nowrap;
  min-height: 80vh; //makes blog take up full height even on pretty large viewports

	section {
		width: 100%;
		// height: 1000px;
		// overflow: auto;
		//display: none;
	}




	// #work, #typefloundry {
	// 	//padding: 0 17px
	// }

	h1{
		color: $light-gray;
		font-size: 2em;

		&:before {
		  display: block;
		  content: " ";
		  margin: -100px 0 2em;
		  height: 100px;
		  visibility: hidden;
      @include breakpoint(phone) {
        margin-bottom: 1em;
      };
		}
	}

  h2 {
    font-weight: bold;
  }

	.front-page-section-intro {
		text-align: center;
		font-style: italic;
		font-family: $serif;
		font-size: 1.25em;
		transform: translateY(-3.5em);
    @include breakpoint(phone) {
      transform: translateY(-3em);
      font-size: 1em;
    };

		// color: $orange;
	}

}

.conceal {
	display:none;
}

.reveal {
	display: block;
	width: 100%;
	opacity: 1;
	transition: 0.35s ease; //0.5s
}

article {
	header {
  		background-color: transparent;
  		color: $body-text;
      transition: .25s ease-in;
	}
  //apply following only to front page list
}
